import { seo, pages } from "projectSettings"
import React from "react"
import { graphql } from "gatsby"
import { GatsbyImage, getImage } from "gatsby-plugin-image"
import { GatsbySeo } from "gatsby-plugin-next-seo"
import Layout from "globalLayoutsIndexPage"
import styled from "styled-components"
import HomePageCover from "projectOrganisms/HomePageCover"
import { usePortfolioAllSinglePages } from "projectGraphqlStaticQueries/portfolio-all"
import openGraphImageUrl from "projectStatic/open-graph/default.jpg"

const PageStyle = styled.div`
  margin-top: 16px;
  .items-list {
    display: grid;
    grid-gap: 16px;
    grid-template-columns: repeat(2, 1fr);
    @media (max-width: 992px) {
      grid-template-columns: repeat(1, 1fr);
    }
  }
  .item {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    text-decoration: none;
    text-align: center;
    margin-bottom: 24px;
    .cover {
      border-radius: 4px;
      background-color: ${({ theme }) => theme.background_primary};
      box-shadow: 1px 0px 0px ${({ theme }) => theme.separator_non_opaque},
        -1px 0px 0px ${({ theme }) => theme.separator_non_opaque},
        0px 1px 0px ${({ theme }) => theme.separator_non_opaque},
        0px -1px 0px ${({ theme }) => theme.separator_non_opaque};
    }
    .caption {
      font-size: 26px;
      line-height: 32px;
      font-weight: normal;
      color: ${({ theme }) => theme.text_secondary};
      margin: 24px;
      max-width: 640px;
      @media (max-width: 1400px) {
        font-size: 22px;
        line-height: 28px;
      }
      @media (max-width: 992px) {
        font-size: 26px;
        line-height: 32px;
      }
      @media (max-width: 992px) {
        font-size: 22px;
        line-height: 28px;
      }
      @media (max-width: 576px) {
        font-size: 18px;
        line-height: 23px;
      }
    }
  }
`

export default function Portfolio(props) {
  const PortfolioPagesData = usePortfolioAllSinglePages()

  return (
    <>
      <GatsbySeo
        title={pages.portfolio.title}
        description={pages.portfolio.description}
        canonical={props.location.href}
        openGraph={{
          url: props.location.href,
          title: pages.portfolio.title,
          description: pages.portfolio.description,
          images: [
            {
              url: `${seo.siteUrl}${openGraphImageUrl}`,
              width: 1200,
              height: 630,
              alt: seo.siteSeoPrefix,
            },
          ],
        }}
      />
      <Layout>
        <section>
          <HomePageCover bg={false}>
            <h1 className="xxxLarge-high-title fw-bold">Portfolio</h1>
            <p
              className="xxxLarge-h1 theme-text-secondary"
              style={{ maxWidth: "768px" }}
            >
              I have posted less than 10% of my works. You can contact me for
              more examples.
            </p>
          </HomePageCover>
        </section>
        <PageStyle as="article" className="container-fluid">
          <div className="items-list">
            {PortfolioPagesData.map(({ node }) => (
              <a
                href={`/${node.slug.replace(/content\//, "")}`}
                className="item"
                key={node.slug}
              >
                <GatsbyImage
                  alt={node.frontmatter.title}
                  image={getImage(node.frontmatter.cover)}
                  className="cover ratio ratio-4x3"
                />
                <h2 className="caption">{node.frontmatter.caption}</h2>
              </a>
            ))}
          </div>
        </PageStyle>
      </Layout>
    </>
  )
}
